body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
}

.thumbnail {
  max-height: 750px;
  height: 750px;
}

#overview {
  display: block; /* Fallback for non-webkit */
  display: -webkit-box;
  max-width: 100%;
  height: 60px; /* Fallback for non-webkit */
  -webkit-line-clamp: 3;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.jumbotron {
  color: #fff;
  background-image: -webkit-gradient(linear, left top, right bottom, from(black), to(#337ab7));
  background-image: -webkit-linear-gradient(top left, black, #337ab7);
  background-image: -o-linear-gradient(top left, black, #337ab7);
  background-image: linear-gradient(to bottom right, black, #337ab7);
}

.form-group {
  -webkit-transform: scale(1.5);
      -ms-transform: scale(1.5);
          transform: scale(1.5);
}

h4 {
  display: inline-block;
  margin: 0 1em;
  padding: 0;
  line-height: 1.5rem;
  font-size: 1.5em;
}

@media only screen and (max-width: 767px) {
  .btn-info {
    margin-bottom: 1em;
    position: relative;
    z-index: 10;
  }
  .form-group {
    -webkit-transform: scale(1);
        -ms-transform: scale(1);
            transform: scale(1);
    margin-bottom: 0;
    text-align: center;
  }
  .form-control {
    display: inline-block;
    width: auto;
  }
  .btn-primary {
    vertical-align: top;
  }
  h4 {
    margin: 0;
  }
  .thumbnail {
    height: auto;
  }
}



